	import {mapState} from 'vuex'
	export default{
		props:{
			data:{
				type:[Array,Object]
			}
		},
		data(){
			return{
				value:''
			}
		},
		computed:{
			...mapState({
				tmpId:state=>state.screen.tmpId,
				query:state=>state.screen.query
			}),
			//checkquery中 相同列集合
			checks(){
				if(this.query && this.query.length>0){
					return this.query.filter(v=>v.columnId == this.data.columnId && v.tmpId == this.tmpId)
				}			
			}			
		},
		created(){
			this.value = '';
			if(this.checks && this.checks.length>0){
				this.value = this.checks[0].value;
			}
		},
		methods:{
			checkFun(val){
				if(val === this.value){
					this.value = '';
				}else{
					this.value = val;
				}
				let query = [];
                if(this.value.length>0){
					query.push({
						tmpId:this.tmpId,
						columnId:this.data.columnId,
						operator:"like",
						value:this.value
					})    
				}
				this.$store.commit("changeCondition",{
									query:query,
									columnId:this.data.columnId,
								});
			}
		}
	}
